<template>
  <div>
    <!-- <div class="right-btns">
      <el-button type="primary" size="default" @click="handleConversion">转换</el-button>
    </div> -->
    <el-row>
      <el-col :span="10">
        <el-radio-group v-model="lRadioIndex" class="">
          <el-radio :label="index" v-for="(item,index) in leftRadio" :key="index">{{item.name}}</el-radio>
        </el-radio-group>
        <div class="grid-content bg-purple">
          <el-input type="textarea" class="textarea" :rows="25" placeholder="请输入内容" v-model="leftTextarea">
          </el-input>
        </div>
      </el-col>
      <el-col :span="1">
        <div class="dot-tip">To</div>
      </el-col>

      <el-col :span="10">
        <el-radio-group v-model="rRadioIndex">
          <el-radio :label="index" v-for="(item,index) in rightRadio" :key="index">{{item.name}}</el-radio>
        </el-radio-group>
        <div class="grid-content bg-purple-light">
          <el-input type="textarea" class="textarea" :rows="25" placeholder="请输入内容" v-model="rightTextarea">
          </el-input>
        </div>
      </el-col>
    </el-row>
  </div>

</template>

<script>
  var htmltoless = require("htmltoless");

  export default {
    name: '',
    data() {
      return {
        lRadioIndex: 0,
        rRadioIndex: 0,
        leftRadio: [{
          name: 'html',
        }],
        rightRadio: [{
          name: 'less'
        }],
        leftTextarea: ``,
        rightTextarea: ``,
      }
    },
    watch: {
      leftTextarea(value) {
        if (value) {
          this.handleConversion();
        }

      }
    },
    methods: {
      handleConversion() {
        var less = htmltoless.parse(this.leftTextarea);
        this.rightTextarea = less;
      }
    }

  }
</script>

<style lang="less" scoped>
  .grid-content {
    margin-top: 20px;
  }

  .dot-tip {
    font-weight: bold;
    text-align: center;
  }

  .textarea {
    min-height: 500px;
  }
</style>


